.button {
    text-decoration: none;
    display: inline-block;
    text-align: center;
    padding: 15px;
    text-transform: uppercase;
    font-size: 20px;
    background-color: #333333;
    border: #333333;
    box-shadow: #333333;
    color: #ffffff;
    transition-duration: 1s;
    min-width: 40%;
}

.button:hover {
    transition-duration: 0.5s;
    background-color: #f2f2f5;
    color: #333333;
}
